<template>
  <div class="selectScreenMode">
    <vx-header :showback="1" :title="$t('selectScreenMode.selectNetworkPort')" :shownext="1" @next="nextStep"></vx-header>
    <div class="networkPort_layout">
      <div class="vx-container">
        <div class="networkProt_list">
          <div :class="{active: item.chacked, isLink: item.link ,isOnline: item.isOnline}" v-for="(item, index) in numList" :key="index" @click="selectPort(index, item)">{{item.num}}</div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  import htp from "../../api/http";
  import url from '../../api/apiurl';
  import { Indicator, Toast } from 'mint-ui';
  import { setLoc, getLoc, setSen, getSen, clearLocalKey } from '../../utils';
  import { mapGetters } from 'vuex';
  export default {
    components: {
      Toast
    },
    created() {
      this.formatPortList();
    },
    data() {
      return {
        numList: [],
        getAdvancedPointsScreens: [],
        netData: {}
      };
    },
    methods: {
      loadamine() {
        if(!window.offline) {
          Indicator.open({
            text: 'Loading...',
            spinnerType: 'triple-bounce'
          });
        }
      },
      //获取高级点评数据
      formatPortList() {

        htp.get(url.getAdvancedPointsScreen).then(res => {
          this.numList = [];
          if(res.status === 10000000) {
            this.getAdvancedPointsScreens = res.data.portList;
            this.getAdvancedPointsScreens.forEach((v, i) => {
              let obj = {};
              obj.portId = v.portId;
              obj.chacked = false;
              obj.num = v.portId;
              obj.portOffsetX = v.portOffsetX;
              obj.portOffsetY = v.portOffsetY;
              obj.portX = v.portX;
              obj.portY = v.portY;
              obj.walkType = v.walkType;
              obj.link = v.link;
              obj.isOnline = false;
              this.numList.push(obj);
            });
          }
          let port = getLoc('selport') || [];
          port.forEach((pv, pi) => {
            this.numList.forEach((nv, i) => {
              if(pv.portId === nv.portId) {
                nv.chacked = true;
                if(nv.link === 1) {
                  nv.isOnline = true;
                }
              }
            });
          });
        });
      },
      //遍历所有端口，筛选出被选中的窗口
      nextStep() {
        let params = [];
        this.numList.forEach((v, i) => {
          if(v.chacked || v.isOnline) {
            params.push(v);
          }
        });
        if(params.length === 0) {
          Toast(this.$t('selectScreenMode.selectNetworkPorts'));
        } else {
          setLoc('port', params);
          this.$router.push({ name: 'advancedScreen', params: { params } });
        }
      },
      selectPort(index, item) {
        this.numList[index].chacked = !item.chacked;
        if(this.numList[index].link === 1) {
          this.numList[index].isOnline = !item.isOnline;
        }
      }
    },
    computed: {
      ...mapGetters(['getPortList'])
    }
  };
</script>
<style scoped lang="less">
  .networkPort_layout {
    .networkProt_list {
      padding: 50px 35px;
      box-sizing: border;
      color: #ddd;
      overflow: hidden;
      > div {
        width: 130px;
        height: 109px;
        background: #41434b;
        float: left;
        margin-left: 33px;
        margin-bottom: 45px;
        line-height: 109px;
        // background: ~"url('../../assets/selectRouMethod/icon_wangkou_normal.png') center center no-repeat / 90%";
        background: ~"url('../../assets/selectRouMethod/icon_wangkou_normal.png')";
        background-position: center;
        background-repeat: no-repeat;
        background-size: 90%;
        font-size: 40px;
        font-weight: 700;
        transition: 0.3s;
      }
      .isLink {
        background: ~"url('../../assets/selectRouMethod/bg.png')";
        background-position: center;
        background-repeat: no-repeat;
        background-size: 90%;
      }
      .active {
        background: ~"url('../../assets/selectRouMethod/icon_wangkou_selected.png')";
        background-position: center;
        background-repeat: no-repeat;
        background-size: 90%;
        // color: #e67007;
      }
      .isOnline {
        background: ~"url('../../assets/selectRouMethod/selonlinNetwork.png')";
        background-position: center;
        background-repeat: no-repeat;
        background-size: 90%;
      }
    }
  }
  // 横屏模式
  @media screen and (orientation: landscape) {
    .networkPort_layout {
      .networkProt_list {
        padding: 50px 35px;
        box-sizing: border;
        color: #ddd;
        overflow: hidden;
        > div {
          width: 130px;
          height: 109px;
          background: #41434b;
          float: left;
          margin-left: 33px;
          margin-bottom: 45px;
          line-height: 109px;
          background: url('../../assets/selectRouMethod/icon_wangkou_normal.png') center center no-repeat / 90%;
          font-size: 40px;
          font-weight: 700;
          transition: 0.3s;
        }
        .active {
          background: url('../../assets/selectRouMethod/icon_wangkou_selected.png') center center no-repeat / 90%;
          color: #e67007;
          border-radius: 8px;
        }
      }
    }
  }
</style>


